a,
a:hover,
a:focus,
a:active {
    text-decoration: none;
    color: inherit;
}

.company-logo {
    color: #626262;
    font-weight: bold;
}

.template1 {
    background-color: rgb(56, 56, 56);
    background-image: url('https://images.pexels.com/photos/1254655/pexels-photo-1254655.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    height: 100%;
    text-transform: uppercase;
}

.template1_body {
    height: 100vh;
}

.template1_header {
    color: #b2b2b2;
    justify-content: space-between;
    font-size: calc(8px + 2vmin);
}

.template1-menu {
    font-size: calc(8px + 2vmin);
}

.text-bold {
    font-weight: bold;
}

.text-center {
    text-align: center;
}