.App {
  background-color: #faddeb;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.App-header {
  background-color: #77B2B7;
  color: #0D0D0D;
  display: flex;
  flex-direction: row;
  font-size: calc(8px + 2vmin);
  font-weight: bold;
  justify-content: space-between;
  height: 30vh;
}

.App-body {
  font-size: calc(8px + 2vmin);
  height: 100%;
}

.App-header-logo {
  align-self: flex-end;
  height: auto;
  width: calc(100vh - 95vh);
}

.App-header-description {
  height: auto;
  text-align: right;
}

.experience {
  background: linear-gradient(to right, #faddeb, #77B2B7);
  font-size: calc(6px + 2vmin);
  justify-content: space-between;
  text-align: end;
}

.experience-image {
  height: auto;
  width: calc(100vh - 90vh);
}

.project-button {
  background: linear-gradient(to right, #faddeb, #77B2B7);
  border: 2px solid #bca5b0;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
}