/* colors
#05445E - navy blue
#189AB4 - blue grotto
#75E6DA - blue green
#D4F1F4 - baby blue
#212121 - header color
#1A1A1A - input color
#454545 - response color
*/

.drizzledroid-container {
    background: linear-gradient(#D4F1F4, #75E6DA);
}

.drizzledroid-header {
    background: linear-gradient(#05445E, #189AB4, #75E6DA, #D4F1F4);
    color: #212121;
    font-size: calc(10px + 2vmin);
    text-align: center;
}

.drizzledroid-button {
    background: #189AB4;
    border: 2px solid #05445E;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    color: white;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
}

.drizzledroid-copy-icon {
    height: 12px;
    width: 12px;
}

.drizzledroid-image {
    width: 60vw;
}

.drizzledroid-input {
    color: #1a1a1a;
    justify-content: space-between;
    font-size: calc(8px + 2vmin);
}

.drizzledroid-response {
    color: #454545;
    justify-content: space-between;
    font-size: calc(8px + 1vmin);
}

.drizzledroid-row {
    border-radius: 10px;
    border: 2px solid #77B2B7;
}

.drizzledroid-row:hover {
    background-color: #05445E;
}

.drizzledroid-width {
    width: 90vw;
}

.drizzledroid-text-bold {
    font-weight: bold;
}